#home{
    display: flex;
    font-family: "Poppins,Sans-serif";
    width: 95%;
    background-image: url(../Assets/particles2.jpg);
    margin: auto;
    border-radius: 20px;
    color: goldenrod;
}
.intro{
    width: 50%;
    padding: 5%;
    justify-content: center;
    /* color: whitesmoke; */
    
}
.intro p{
    font-size: 20px;
    font-weight: 500;
    
}
.imgCont{
    width: 50%;
    justify-content: center;
    margin: auto;
    padding: 2%;
}
.nezu{
    margin: auto;
    width: 250px;
    height: 300px;
    border-radius: 10px;
    justify-content: center;
    /* border: 2px solid red; */
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}

.about{
    margin: auto;
    margin-top: 20px;
    padding: 15px;
}
#about .text{
    line-height: 28px;
    justify-content:center;
    font-weight: 400;
    font-size: 18px;
}

#skills{
    width: 95%;
    margin: auto;
    color: goldenrod;
    margin-top: 20px;
    border-radius: 20px;
    background-image: url(../Assets/nav.gif);
    padding: 20px;
}

@media (min-width:501px) and  (max-width: 1000px){
    #home {
        display: block;
        justify-content: center;
        margin: auto;
    }
    .intro{
        width: 100%;
        justify-content: center;
        padding: auto;
    }
    .imgCont{
        width: 100%;
        justify-content: center;
        margin: auto;
        /* border: 2px solid green; */
        padding: auto;
    }
    .intro .name{
        font-size: 58px;
    }
    .intro .description{
        font-size: 30px;
    }
    .nezu{
        margin: auto;
        width: 95%;
        height: auto;
        justify-content: center;
        /* border: 2px solid red; */
    }


}
@media  (max-width: 500px){
    #home {
        display: block;
        justify-content: center;
        padding: 0;
        margin: auto;
    }
    .intro{
        width: 100%;
        justify-content: center;
        padding: auto;
    }
    .imgCont{
        width: 100%;
        justify-content: center;
        margin: auto;
        padding: auto;
    }
    .intro .name{
        font-size: 44px;
    }
    .intro .description{
        font-size: 24px;
    }
    .nezu{
        margin: auto;
        width: 95%;
        height: auto;
        justify-content: center;
    }
}